.couple-section {
	position: relative;
	text-align: center;
	overflow: hidden;
	.and {
		display: flex;
		justify-content: center;
		align-items: center;
		width: 100%;
		position: relative;
		padding: 1rem 0;
		span {
			position: relative;
		}
	}
	.couple-wrapper {
		position: relative;
		.gadang-top {
			position: relative;
			width: 75%;
			margin: -9.5rem auto -2rem;
			transform: scaleY(-1);
		}
		.gadang-bottom {
			position: relative;
			width: 75%;
			margin: -2rem auto -5rem;
		}
	}
	.couple-body {
		position: relative;
	}
	.couple-content {
		position: relative;
		margin-bottom: 3rem;
	}
	.couple {
		position: relative;
		.couple-description {
			position: relative;
			padding: 1rem;
			text-align: center;
		}
		.couple-parent {
			position: relative;
			width: 100%;
		}
		.sosmed-wrap {
			position: relative;
			width: fit-content;
			max-width: 100%;
			background: var(--color-secondary);
			padding: 0.5rem 0.6rem;
			border-radius: 50px;
			margin: 0 auto;
			.sosmed {
				color: var(--color-light);
				text-decoration: none;
				display: block;
				width: fit-content;
				margin: 0 auto;
				small {
					align-items: center;
					gap: 0.25rem;
					display: flex;
					justify-content: center;
				}
			}
			.fab {
				font-size: 18px;
				@include max-md {
					font-size: 16px;
				}
			}
		}
	}
}
