.cover-section {
	position: fixed;
	z-index: 1000;
	transition: all 1s ease-out;
	background-color: var(--color-main);
	overflow: hidden;
	width: 100%;
	&.cover-opened {
		transform: translateY(-100%);
	}
	.cover-wrapper {
		position: relative;
		display: flex;
		justify-content: center;
		padding: 6rem 0 3rem;
		transition: opacity 1s ease;
		height: 100vh;
		@include max-lg {
			padding: 6rem 0 7rem;
		}
		.cover-content {
			position: relative;
			width: 100%;
			height: 100%;
			.cover-body {
				position: relative;
				.cover-description {
					position: relative;
					text-align: center;
					.cover-event {
						position: relative;
						margin-bottom: 1rem;
					}
					.greeting-wrapper {
						position: relative;
						margin-bottom: 1rem;
					}
					.title {
						padding: 1rem 0;
						margin-bottom: 0;
					}
					p {
						margin-bottom: 0;
					}
					h6 {
						margin-bottom: 0;
						font-weight: 600;
					}
				}
			}
			.elements-widget {
				position: relative;
				width: 100%;
				.btn-custom {
					display: flex;
					align-items: center;
					justify-content: center;
					gap: 0.75rem;
				}
			}
		}
	}
}
