.thank-section {
	position: relative;
	padding: 5rem 0 0;
	background-color: var(--color-primary);
	@include max-md {
		padding: 3rem 0 0;
	}
	.thank-wrapper {
		position: relative;
		background-color: var(--color-main);
		border-radius: 150px;
		.thank {
			position: relative;
			&-body {
				position: relative;
				padding: 10rem 2rem 20rem;
				@include max-md {
					padding: 8rem 1rem 18rem;
				}
			}
		}
	}
}
.footer {
	padding: 1rem 0;
	position: relative;
	text-align: center;
	background-color: var(--color-secondary);
	color: var(--text-button);
	small {
		position: relative;
	}
}
