.venue-section {
	position: relative;
	padding: 5rem 0;
	background-color: var(--color-primary);
	@include max-md {
		padding: 3rem 0;
	}
	.venue-wrapper {
		position: relative;
		z-index: 3;
		.venue-description {
			position: relative;
			margin: 0 auto 5rem;
			z-index: 2;
			text-align: center;
			@include max-lg {
				margin-bottom: 3rem;
			}
		}
		.venue-content {
			position: relative;
			background-color: var(--color-main);
			mask: var(--mask);
			padding: 3rem 2rem;
			@include max-md {
				padding: 2rem 1rem;
			}
			.card {
				background-color: transparent;
				border: none;
				.card-body {
					position: relative;
					padding: 2rem 1rem;
					h4 {
						padding: 0 0 0.25rem;
						font-size: 28px;
					}
					hr {
						display: block;
						width: 10%;
						height: 2px;
						margin: 0 auto 1rem;
					}
					.event-description {
						position: relative;
						margin-bottom: 1rem;
						p {
							margin-bottom: 0;
						}
						span {
							font-size: 36px;
							font-family: $font-secondary;
						}
					}
					.event-name {
						position: relative;
						width: 100%;
						padding-bottom: 1rem;
						.date {
							position: relative;
							width: 100%;
							display: flex;
							justify-content: flex-start;
							align-items: center;
							padding: 1rem 0;
							span {
								display: block;
								font-size: 28px;
								letter-spacing: 0.5rem;
							}
							.day {
								font-family: $font-secondary;
								line-height: 2.5;
								margin-bottom: -1rem;
								letter-spacing: normal;
							}
						}
						span {
							font-size: 28px;
						}
						p {
							margin-bottom: 0;
							font-weight: 600;
						}
					}
					.event-place {
						position: relative;
						padding-bottom: 1rem;
					}
				}
			}
		}
	}
	.prokes-wrapper {
		margin: 3rem 0 0;
		position: relative;
		padding: 2rem;
		text-align: center;
		z-index: 2;
		.title-section {
			&::before {
				all: unset;
			}
		}
		@include max-md {
			padding: 2rem 0;
		}
		* {
			position: relative;
		}
	}
	.prokes-wrap {
		display: flex;
		gap: 0.75rem;
		justify-content: center;
		align-items: flex-start;
		flex-wrap: wrap;
		text-align: center;
		.prokes {
			display: flex;
			flex-direction: column;
			align-items: center;
			justify-content: flex-start;
			width: 132px;
			@include max-md {
				width: 120px;
			}
			@include max-xs {
				width: 45%;
			}
		}
	}
	.row-venue {
		row-gap: 3rem;
	}
	.animation-loop {
		.st1 {
			will-change: transform;
			animation: zoomInRing 2000ms ease-out infinite;
		}
		.dot {
			will-change: transform;
			animation: fadeBoing 2000ms ease-out infinite;
			transform-origin: 50% 75%;
		}
		#Layer_3 {
			will-change: transform;
			animation: boing 1000ms ease-out infinite alternate;
		}
	}

	@keyframes zoomInRing {
		0% {
			opacity: 0;
			transform: scale3d(0.5, 0.5, 0.5);
		}
		40% {
			opacity: 1;
		}
		70% {
			opacity: 0;
			transform: scale3d(1.25, 1.25, 1.25);
		}
		100% {
			opacity: 0;
		}
	}

	@keyframes boing {
		0% {
			transform: translate3d(0, 0, 0);
		}
		100% {
			transform: translate3d(0, -5rem, 0);
		}
	}

	@keyframes fadeBoing {
		0% {
			opacity: 1;
		}
		50% {
			transform: scale3d(0.25, 0.25, 0.25);
			opacity: 0;
		}
		100% {
			transform: scale3d(1, 1, 1);
			opacity: 1;
		}
	}
}

.maps-embed {
	width: 100%;
	height: 100%;
	position: absolute;
	top: 0;
	left: 0;
}
